/* Mantém o estilo principal */
.pantry-detail-container {
    padding: 20px;
    background-color: rgba(255, 255, 255, 0.8);
    border-radius: 15px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
    max-width: 1200px;
    margin: 0 auto;
}

.pantry-header {
    display: flex;
    align-items: center;
    margin-bottom: 30px;
}

.pantry-image {
    width: 150px;
    height: 150px;    
    object-fit: contain;  
    border-radius: 15px;
    margin-right: 20px;
}

.pantry-info {
    color: #333;
}

.pantry-info h2 {
    font-size: 2rem;
    margin-bottom: 10px;
}

.pantry-info p {
    font-size: 1rem;
    margin: 5px 0;
}

.low-quantity {
    color: #ff4d4d;
    font-weight: bold;
}

.pantry-items {
    display: flex;
    gap: 20px;
    flex-wrap: wrap;
    justify-content: center;
}

.pantry-item {
    background: #f9f9f9;
    border-radius: 10px;
    padding: 15px;
    width: 150px;
    text-align: center;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s;
}

.pantry-item:hover {
    transform: translateY(-5px);
}

.item-image {
    width: 100px;
    height: 100px;
    object-fit: cover;
    margin-bottom: 10px;
}

.low-stock {
    border: 2px solid #ff4d4d;
}

/* Estilos para as abas */
.pantry-tabs {
    display: flex;
    gap: 10px;
    margin-bottom: 20px;
    justify-content: center;
}

.tab-button {
    padding: 10px 20px;
    border: none;
    background-color: #e0e0e0;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s, color 0.3s;
    font-size: 1rem;
    color: #333;
}

.tab-button.active {
    background-color: #3498db;
    color: #fff;
    font-weight: bold;
}

.tab-content {
    margin-top: 20px;
    text-align: center;
}

/* Botões dentro das abas */
.add-items-auto-button,
.view-cart-button {
    margin: 10px 0;
    padding: 10px 20px;
    background-color: #67cf89;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s;
}

.add-items-auto-button:hover,
.view-cart-button:hover {
    background-color: #57b77a;
}

/* Estilização para a aba de histórico e lista */
.tab-shopping-list .add-items-auto-button {
    margin-top: 15px;
}

.tab-history p {
    color: #555;
    font-size: 1rem;
    margin: 10px 0;
}

.quantity-input {
    width: 50px;
    text-align: center;
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 5px;
    padding: 5px;
    margin: 0 5px;
}
.quantity-input:focus {
    outline: none;
    border-color: #67cf89;
    box-shadow: 0 0 5px rgba(103, 207, 137, 0.5);
}



/* Estilos para os cards de itens */
.shopping-list-item-card {
    background: #ffffff;
    border-radius: 10px;
    padding: 15px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    margin: 10px auto;
    max-width: 300px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
}

.item-name {
    font-size: 1.2rem;
    font-weight: bold;
    margin-bottom: 10px;
    color: #333;
}

.item-quantity-control {
    display: flex;
    align-items: center;
    gap: 10px;
    margin-top: 10px;
}

.item-quantity-control button {
    background-color: #3498db;
    color: #fff;
    border: none;
    padding: 5px 10px;
    border-radius: 5px;
    cursor: pointer;
    font-size: 1rem;
    transition: background-color 0.3s;
}

.item-quantity-control button:hover {
    background-color: #2980b9;
}

.item-quantity-control span {
    font-size: 1.2rem;
    font-weight: bold;
    color: #555;
}

.item-quantity-control button:disabled {
    background-color: #ccc;
    cursor: not-allowed;
}
